import { TransactionBlock } from "@mysten/sui.js/transactions";
import { PACKAGE_ID, REGISTRY } from '../config/constants';
import { useWallet } from '@suiet/wallet-kit';

/**
 * Hook to create a raw transaction for minting an NFT
 * @returns A function to execute a mint NFT transaction.
 */
export function useMintNFT() {
    const wallet = useWallet();

    const mintNFT = async (gridSize) => {
        if (!wallet.connected || !wallet.account) {
            console.error('Wallet not connected');
            return;
        }

        try {

            const tx = new TransactionBlock();
            const [coin] = tx.splitCoins(tx.gas, [1]);

            tx.moveCall({
                target: `${PACKAGE_ID}::wallet_art_v2::new`,
                arguments: [
                    tx.object(REGISTRY), 
                    tx.object('0x8'),    
                    coin, 
                    tx.pure(gridSize),   
                ],
            });

            const result = await wallet.signAndExecuteTransactionBlock({
                transactionBlock: tx,
            });

            await new Promise((resolve) => setTimeout(resolve, 500)); // Ensure the transaction settles

            if (result) {
                const txId = result.digest;
                const url = `https://explorer.sui.io/txblock/${txId}?network=mainnet`;
                console.log('Transaction successful:', url);
            } else {
                console.error('Transaction failed or result is unexpected:', result);
            }
        } catch (error) {
            console.error('Error submitting transaction:', error);
        }
    };

    return { mintNFT };
}

/**
 * Hook to create a raw transaction for regenerating NFT art.
 * @returns A function to execute a regenerate art transaction.
 */
export function useRegenerateArt() {
    const wallet = useWallet();

    const regenerateArt = async (nft) => {
        if (!wallet.connected || !wallet.account) {
            console.error('Wallet not connected');
            return;
        }

        const tx = new TransactionBlock();

        tx.moveCall({
            target: `${PACKAGE_ID}::wallet_art_v2::regenerate_art`,
            arguments: [
                tx.object(nft),       
                tx.object('0x8'),    
            ],
        });

        try {
            const result = await wallet.signAndExecuteTransactionBlock({
                transactionBlock: tx,
            });

            await new Promise((resolve) => setTimeout(resolve, 500));

            if (result) {
                const txId = result.digest;
                const url = `https://explorer.sui.io/txblock/${txId}?network=mainnet`;
                console.log('Transaction successful:', url);
            } else {
                console.error('Transaction failed or result is unexpected:', result);
            }
        } catch (error) {
            console.error('Error submitting transaction:', error);
        }
    };

    return { regenerateArt };
}
